@import '../../../styles/variables';
@import '../../../styles/main';
@import '../../../styles//mixins';

.primaryBold {
  font-weight: $extraBoldFontWeight;
}

.secondaryBold {
  font-weight: $lightFontWeight;
}

.navBar {
  @include boxShadowGenerator($boxShadow);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-right: 1.2rem;
  padding-left: 1.2rem;
  position: relative;
  z-index: 1;
  > * {
    transition: transform 0.1s ease-in;
  }

  a:first-of-type {
    font-size: 14px;
    color: inherit;
    text-decoration: none;
  }
  button {
    border: none;
    background: none;
    font-size: 13px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    outline: none;
    i {
      margin-right: 10px;
    }
  }

  @media screen and (min-width: $desktopBreakPoint) {
    padding-left: 7rem;
    padding-right: 7rem;
  }
  @media screen and (hover: hover) {
    a:hover,
    button:hover {
      transform: scale(1.05);
    }
  }
}
